//* Ð Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŒÑÐºÐ¸Ð¹ ÐºÐ»Ð°ÑÑ Ð²ÑÐµÑ… Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð²
export default class Popup {
  constructor(popupElement) {
    this._popupElement = popupElement;
    this._popupOverlay = this._popupElement.querySelector(".popup__overlay");
    this._popupCloseButton = this._popupElement.querySelector(
      ".popup__close-button"
    );
    this._handleEscClose = this._handleEscClose.bind(this);
  }

  //* Ð Ð°ÑÑ‡ÐµÑ‚ ÑˆÐ¸Ñ€Ð¸Ð½Ñ‹ Ð²ÐµÑ€Ñ‚Ð¸ÐºÐ°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÑÐºÑ€Ð¾Ð»Ð»Ð°
  //* ÐŸÑ€Ð¸ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ð¸ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ð±Ð»Ð¾ÐºÐ¸Ñ€ÑƒÐµÑ‚ÑÑ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ° ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹ Ð¸ Ñ‡Ñ‚Ð¾Ð±Ñ‹ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚ Ð½Ðµ Ð´ÐµÑ€Ð³Ð°Ð»ÑÑ, ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ðµ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ‚ÑÑ Ð³Ñ€Ð°Ð½Ð¸Ñ†Ð° ÑÐ¿Ñ€Ð°Ð²Ð° ÑˆÐ¸Ñ€Ð¸Ð½Ð¾Ð¹ Ñ Ð¿Ð¾Ð»Ð¾ÑÑƒ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ¸
  _calcScrollWidth() {
    const auxiliaryBlock = document.createElement("div");
    auxiliaryBlock.style.width = "50px";
    auxiliaryBlock.style.height = "50px";
    auxiliaryBlock.style.overflowY = "scroll";
    auxiliaryBlock.style.visibility = "hidden";

    document.body.appendChild(auxiliaryBlock);
    const scrollWidth = auxiliaryBlock.offsetWidth - auxiliaryBlock.clientWidth;
    auxiliaryBlock.remove();

    return scrollWidth;
  }

  //* ÐœÐµÑ‚Ð¾Ð´ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ð¸ Ð±Ð»Ð¾ÐºÐ¸Ñ€Ð¾Ð²ÐºÐ¸ ÑÐºÑ€Ð¾Ð»Ð»Ð° ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹
  open() {
    const scrollWidth = this._calcScrollWidth();
    document.addEventListener("keyup", this._handleEscClose);
    this._popupElement.classList.add("popup_opened");
    document.body.style.overflow = "hidden";

    //* Ð”Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð³Ñ€Ð°Ð½Ð¸Ñ†Ñ‹ Ñ ÑˆÐ¸Ñ€Ð¸Ð½Ð¾Ð¹ Ñ€Ð°Ð²Ð½Ð¾Ð¹ ÑˆÐ¸Ñ€Ð¸Ð½Ðµ ÑÐºÑ€Ð¾Ð»Ð»Ð°
    document.body.style.borderRight = `${scrollWidth}px solid black`;
  }

  //* ÐœÐµÑ‚Ð¾Ð´ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  close() {
    document.removeEventListener("keyup", this._handleEscClose);
    this._popupElement.classList.remove("popup_opened");
    document.body.style.overflow = "";
    document.body.style.borderRight = "none";
  }

  //* ÐœÐµÑ‚Ð¾Ð´ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð½Ð° ESC
  _handleEscClose(evt) {
    if (evt.key === "Escape") {
      this.close();
    }
  }

  //* Ð£ÑÑ‚Ð°Ð½Ð¾Ð²ÐºÐ° ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»ÐµÐ¹
  setEventListeners() {
    this._popupCloseButton.addEventListener("click", () => {
      this.close();
    });
    this._popupOverlay.addEventListener("click", () => {
      if (this._popupElement.classList.contains("popup_opened")) {
        this.close();
      }
    });
  }
}
