import "./index.css";

import { FormValidator } from "../scripts/components/FormValidator.js";
import Section from "../scripts/components/Section.js";
import { Card } from "../scripts/components/Card.js";
import UserInfo from "../scripts/components/UserInfo.js";
import PopupWithForm from "../scripts/components/PopupWithForm.js";
import PopupWithImage from "../scripts/components/PopupWithImage.js";
import PopupConfirm from "../scripts/components/PopoupConfirm";
import Api from "../scripts/components/Api.js";
import {
  initialCards as items,
  editProfilePopup,
  avatarEditPopup,
  profileEditButton,
  profileAvatarEditButton,
  addCardPopup,
  cardDeletePopup,
  profileJob,
  profileName,
  profileAvatar,
  elementsList,
  openPicturePopup,
  editProfileNameInput,
  editProfileJobInput,
  enableValidation,
  profileAddButton,
} from "../scripts/utils/constants.js";

window.addEventListener("DOMContentLoaded", () => {
  const api = new Api({
    serverUrl: "https://mesto.nomoreparties.co/v1/cohort-19/",
    token: "4fc24223-fbb6-4e5b-bedd-d51fcb2b9911",
  });

  //* ÐŸÐµÑ€ÐµÐ¼ÐµÐ½Ð½Ð°Ñ Ñ id Ð¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÐµÐ»Ñ
  let userId;

  //* Ð—Ð°Ð¿Ñ€Ð¾Ñ Ð´Ð°Ð½Ð½Ñ‹Ñ… ÑÐµÑ€Ð²ÐµÑ€Ð° Ð´Ð»Ñ Ð¿Ñ€ÐµÐ²Ð¾Ð¹ Ð¾Ñ‚Ñ€Ð¸ÑÐ¾Ð²ÐºÐ¸ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹
  Promise.all([api.getUserInfo()]) //api.getInitialCards()
    .then((res) => {
      console.log(res);
      const userData = res[0]; //* ÐžÐ±ÑŠÐµÐºÑ‚ Ñ Ð´Ð°Ð½Ð½Ñ‹Ð¼Ð¸ Ð¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÐµÐ»Ñ
      console.log(userData);
      userId = userData._id;
      userInfo.setUserInfo(userData);
      userInfo.setUserAvatar(userData);
      // section._renderer(res[1]);
    })
    .catch((err) => console.log(err));

  //* ÐŸÐ¾Ð¿Ð°Ð¿ Ñ Ñ„Ð¾Ñ‚Ð¾
  const popupWithImage = new PopupWithImage(openPicturePopup);
  popupWithImage.setEventListeners();

  //* ÐžÑ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ñ Ñ„Ð¾Ñ‚Ð¾
  const openImagePopup = (evt) => {
    const data = {
      image: evt.target.src,
      text: evt.target
        .closest(".elements__list-item")
        .querySelector(".elements__text").textContent,
    };
    popupWithImage.open(data);
  };

  //* Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸
  const createCard = (data) => {
    const card = new Card(data, ".cardTemplate", openImagePopup);
    const cardElement = card.createCardElement(data);
    return cardElement;
  };

  //! ÐŸÐ¾Ð¿Ð°Ð¿ ÑƒÐ´Ð°Ð»ÐµÐ½Ð¸Ñ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸
  const popupDeleteImage = new PopupConfirm(cardDeletePopup, {
    formSubmitCallBack: (data) => {
      api
        .deleteCard(data._id)
        .then(() => {
          data.item.remove();
          popupDeleteImage.close();
        })
        .catch((err) => console.log(err));
    },
  });
  popupDeleteImage.setEventListeners();

  //* Ð“ÐµÐ½ÐµÑ€Ð°Ñ†Ð¸Ñ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐµÐº
  const section = new Section(
    {
      renderItems: (data) => {
        section.addItem(createCard(data));
      },
    },
    elementsList
  );
  section.renderItems(items);

  //* ÐŸÐ¾Ð¿Ð°Ð¿ Ñ€ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ñ Ð¿Ñ€Ð¾Ñ„Ð¸Ð»Ñ
  const userInfo = new UserInfo({ profileName, profileJob, profileAvatar });

  const editPopup = new PopupWithForm(editProfilePopup, {
    formSubmitCallBack: (data, button) => {
      button.textContent = "Ð¡Ð¾Ñ…Ñ€Ð°Ð½ÐµÐ½Ð¸Ðµ...";
      api
        .editProfile(data)
        .then((res) => {
          userInfo.setUserInfo(res);
          editPopup.close();
        })
        .catch((err) => console.log(err))
        .finally(() => {
          button.textContent = "Ð¡Ð¾Ñ…Ñ€Ð°Ð½Ð¸Ñ‚ÑŒ";
        });
    },
  });
  editPopup.setEventListeners();

  //* ÐŸÐ¾Ð¿Ð°Ð¿ Ñ€ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ñ Ð°Ð²Ð°Ñ‚Ð°Ñ€ÐºÐ¸
  const avatarEdit = new PopupWithForm(avatarEditPopup, {
    formSubmitCallBack: (data, button) => {
      button.textContent = 'Ð¡Ð¾Ñ…Ñ€Ð°Ð½ÐµÐ½Ð¸Ðµ...';
      api
        .editAvatar(data)
        .then((res) => {
          userInfo.setUserAvatar(res);
          avatarEdit.close();
        })
        .catch((err) => console.log(err))
        .finally(() => {
          button.textContent = 'Ð¡Ð¾Ñ…Ñ€Ð°Ð½Ð¸Ñ‚ÑŒ';
        })
    }
  });
  avatarEdit.setEventListeners();

  //* ÐŸÐ¾Ð¿Ð°Ð¿ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸
  const addNewCardPopup = new PopupWithForm(addCardPopup, {
    formSubmitCallBack: (data) => {
      const item = {
        name: data.placeName,
        link: data.placeLink,
      };
      section.addItem(createCard(item), true);
      addNewCardPopup.close();
    },
  });
  addNewCardPopup.setEventListeners();

  //* Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÐºÐ»Ð°ÑÑÐ¾Ð² Ð²Ð°Ð»Ð¸Ð´Ð°Ñ†Ð¸Ð¸
  const editPopupValidation = new FormValidator(
      enableValidation,
      editProfilePopup
    ),
    addPopupValidation = new FormValidator(enableValidation, addCardPopup),
    avatarEditPopopValidation = new FormValidator(enableValidation, avatarEditPopup);

  //* ÐÐºÑ‚Ð¸Ð²Ð°Ñ†Ð¸Ñ Ð²Ð°Ð»Ð¸Ð´Ð°Ñ†Ð¸Ð¸
  editPopupValidation.enableValidation();
  addPopupValidation.enableValidation();
  avatarEditPopopValidation.enableValidation();

  //* Ð£ÑÑ‚Ð°Ð½Ð¾Ð²ÐºÐ° ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»ÐµÐ¹
  profileEditButton.addEventListener("click", () => {
    const data = userInfo.getUserInfo();
    editProfileNameInput.value = data.name;
    editProfileJobInput.value = data.job;
    editPopupValidation.hideAllErrors();
    editPopup.open();
    profileEditButton.blur();
  });
  profileAddButton.addEventListener("click", () => {
    addNewCardPopup.open();
    profileAddButton.blur();
    addPopupValidation.hideAllErrors();
  });
  profileAvatarEditButton.addEventListener("click", () => {
    avatarEdit.open();
    profileAvatarEditButton.blur();
    avatarEditPopopValidation.hideAllErrors();
  })
});
