import Popup from "./Popup.js";

//* ÐšÐ»Ð°ÑÑ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ñ Ñ„Ð¾Ñ€Ð¼Ð¾Ð¹
export default class PopupWithForm extends Popup {
  constructor(popupElement, { formSubmitCallBack }) {
    super(popupElement);
    this._formSubmitCallBack = formSubmitCallBack;
    this._formSubmit = this._formSubmit.bind(this);
    this._form = this._popupElement.querySelector(".popup__form");
    this._inputs = Array.from(this._form.querySelectorAll(".popup__input"));
    this._submitButton = this._form.querySelector(".popup__button");
  }

  //* Ð¡Ð°Ð±Ð¼Ð¸Ñ‚ Ñ„Ð¾Ñ€Ð¼Ñ‹
  _formSubmit(evt) {
    evt.preventDefault();
    this._formSubmitCallBack(this._getInputValues(), this._submitButton);
  }

  //* ÐœÐµÑ‚Ð¾Ð´ ÑÐ±Ð¾Ñ€Ð° Ð´Ð°Ð½Ð½Ñ‹Ñ… ÑÐ¾ Ð²ÑÐµÑ… Ð¿Ð¾Ð»ÐµÐ¹ Ñ„Ð¾Ñ€Ð¼Ñ‹
  _getInputValues() {
    const data = {};
    this._inputs.forEach(input => {
      data[input.name] = input.value;
    });
    return data;
  }

  //* ÐŸÐµÑ€ÐµÐ·Ð°Ð¿Ð¸ÑÑŒ Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŒÑÐºÐ¾Ð³Ð¾ Ð¼ÐµÑ‚Ð¾Ð´Ð° Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð°
  close() {
    super.close();
    this._form.reset();
  }

  //* ÐŸÐµÑ€ÐµÐ·Ð°Ð¿Ð¸ÑÑŒ Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŒÑÐºÐ¾Ð³Ð¾ Ð¼ÐµÑ‚Ð¾Ð´Ð° ÑƒÑÑ‚Ð°Ð½Ð¾Ð²ÐºÐ¸ ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»ÐµÐ¹
  setEventListeners() {
    super.setEventListeners();
    this._form.addEventListener("submit", this._formSubmit);
  }
}
