export class Card {
  constructor(data, cardSelector, handleCardClick) {
    this._data = data;
    this._cardSelector = cardSelector;
    this._handleCardClick = handleCardClick;
    this._element = this._getTemplate();
    this._likeButton = this._element.querySelector(".elements__like-button");
    this._deleteButton = this._element.querySelector(
      ".elements__delete-button"
    );
  }

  //* ÐŸÐ¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ ÑˆÐ°Ð±Ð»Ð¾Ð½Ð° Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÐ¸
  _getTemplate() {
    const cardElement = document
      .querySelector(this._cardSelector)
      .content.querySelector(".elements__list-item")
      .cloneNode(true);
    return cardElement;
  }

  //* Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸
  createCardElement() {
    this._setEventListeners();
    const cardImage = this._element.querySelector(".elements__picture");
    cardImage.alt = `${this._data.name} by Brent Olson`;
    cardImage.src = this._data.link;
    this._element.querySelector(
      ".elements__text"
    ).textContent = this._data.name;

    return this._element;
  }

  //* Ð£ÑÑ‚Ð°Ð½Ð¾Ð²ÐºÐ° ÑÐ»ÑƒÑˆÐ°Ñ‚ÐµÐ»ÐµÐ¹
  _setEventListeners() {
    this._deleteButton.addEventListener("click", () => this._deleteCard());

    this._likeButton.addEventListener("click", () => this._likeToggler());

    this._element
      .querySelector(".elements__picture")
      .addEventListener("click", this._handleCardClick);
  }

  //* ÐŸÐµÑ€ÐµÐºÐ»ÑŽÑ‡ÐµÐ½Ð¸Ðµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ñ Ð»Ð°Ð¹ÐºÐ°
  _likeToggler() {
    this._likeButton.classList.toggle("heartbeat");
    this._likeButton.classList.toggle("elements__like-button_active");
  }

  //* Ð£Ð´Ð°Ð»ÐµÐ½Ð¸Ðµ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐ¸
  _deleteCard() {
    this._element.remove();
    this._element = null;
  }
}
